import React from 'react';

import { connect } from 'react-redux';
import { 
    AddCustomerToList,
    AddCustomersToList
} from "../actions/actionFuncs";

import {
    customers
} from '../data';

class ListCustomersComponent extends React.Component {
    
    constructor( props ) {
        super( props )

        this.state = {
            filter: {
                'source': '',
                'type': '',
                'status': ''
            }
        }
    }
    

    _changeSelect = (e)=> {
        let {
            filter
        } = this.state;

        filter[e.target.name] = e.target.value;

        this.setState({
            filter
        });

    }

    _addCustomerToList = ( customer ) => {
        this.props.AddCustomerToList( customer )
    }

    render() {

        let {
            filter
        } = this.state;

        return (
            <div className="customers">
                <div className="filter">
                    <div className="form-group">
                        <label htmlFor="source">Nguồn từ</label>
                            <select className="form-control" id="source" name="source" onChange={ this._changeSelect } value={ filter['source'] }>
                                <option value="">Tất cả</option>
                                <option value="GEARN_COM" >GEARVN COM</option>
                                <option value="GEARN_NEWS" >GEARVN NEWS</option>
                                <option value="FACEBOOK" >FACEBOOK</option>
                                <option value="GOOGLE" >GOOGLE</option>
                            </select>
                    </div>

                    <div className="form-group">
                        <label htmlFor="type">Loại</label>
                            <select className="form-control" id="type" name="type" onChange={ this._changeSelect } value={ filter['type'] }>
                                <option value="">Tất cả</option>
                                <option value="LEAD" >Tiềm năng</option>
                                <option value="CUSTOMER" >Khách quen</option>
                                <option value="OTHER" >Khác</option>
                            </select>
                    </div>

                    <div className="form-group">
                        <label htmlFor="status">Trạng thái</label>
                            <select className="form-control" id="status" name="status" onChange={ this._changeSelect } value={ filter['status'] }>
                                <option value="">Tất cả</option>
                                <option value="SUBSCRIBE" >Đăng ký</option>
                                <option value="UNSUBSCRIBE" >Hủy đăng ký</option>
                                <option value="DRAFT" >Rác</option>
                                <option value="OTHER" >Khác</option>
                            </select>
                    </div>
                </div>
                <h4>Danh sách người dùng</h4>
                <div className="lst-customer">
                    {
                        customers.map( ( item, index ) => {
                            if ( filter['source'] !== '' && item.source.toLowerCase() !== filter['source'].toLowerCase() ) return null;
                            if ( filter['type'] !== '' && item.type.toLowerCase() !== filter['type'].toLowerCase() ) return null;
                            if ( filter['status'] !== '' && item.status.toLowerCase() !== filter['status'].toLowerCase() ) return null;
                            return (
                                <div className="customer" key={index}>
                                    <div className="label">
                                        <input type="checkbox" value={item.email}/>
                                        <span>{item.email}</span>
                                        <i className="fas fa-plus" onClick={ ()=> this._addCustomerToList( item ) }></i>
                                    </div>
                                </div>
                            )
                        } )
                    }
                </div>
                <div className="actions">
                    <button type="button" className="btn btn-warning">
                        <i className="fas fa-plus"></i>
                        Chọn tất cả
                    </button>

                    <button type="button" className="btn btn-success">
                        Xác nhận
                    </button>
                </div>
            </div>
        )
    }

}

// create container

const mapStateToProps = state => ({
    customerSelected : state.CustomerSelectedReducer
});

const mapDispatchToProps = dispatch => ({
    AddCustomerToList : customer => dispatch( AddCustomerToList( customer ) ),
    AddCustomersToList : customers => dispatch( AddCustomersToList( customers ) )
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ListCustomersComponent);