
import { 
    ADD_CUSTOMER,
    ADD_CUSTOMERS,
    REMOVE_CUSTOMER,
    REMOVE_CUSTOMERS
} from "./action_variables";

const AddCustomerToList = customer => ({
    type: ADD_CUSTOMER,
    data: customer
})

const AddCustomersToList = customers => ({
    type: ADD_CUSTOMERS,
    data: customers
})

const RemoveCustomerFromList = customer =>({
    type: REMOVE_CUSTOMER,
    data: customer
})

const RemoveCustomersFromList = customers =>({
    type: REMOVE_CUSTOMERS,
    data: customers
})

export {
    AddCustomerToList,
    AddCustomersToList,
    RemoveCustomerFromList,
    RemoveCustomersFromList
}