import React from 'react';

import { connect } from 'react-redux';
import { 
    RemoveCustomerFromList,
    RemoveCustomersFromList
} from "../actions/actionFuncs";

class ListCustomerSelectedComponent extends React.Component {

    constructor( props ) {
        super( props )
    }

    _removeCustomerToList = ( customer ) => {
        this.props.RemoveCustomerFromList( customer )
    }

    render() {
        let customers = this.props.customerSelected;

        return (
            <div className="selected-customers">
                <div className="header-title">
                    <h4>Gửi mail tới</h4>
                </div>
                <div className="lst-customer">
                    {
                        customers.map( ( item, index ) => {
                            return (
                                <div className="customer" key={index}>
                                    <div className="label">
                                        <input type="checkbox" value={item.email}/>
                                        <span>{item.email}</span>
                                        <i className="fas fa-minus" onClick={ ()=> this._removeCustomerToList( item ) }></i>
                                    </div>
                                </div>
                            )
                        } )
                    }
                </div>
                <div className="actions">
                    <button type="button" className="btn btn-warning">
                        <i className="fas fa-plus"></i>
                        Chọn tất cả
                    </button>

                    <button type="button" className="btn btn-success">
                        Clear
                    </button>
                </div>
            </div>
        )
    }

}

// create container

const mapStateToProps = state => ({
    customerSelected : state.CustomerSelectedReducer
});

const mapDispatchToProps = dispatch => ({
    RemoveCustomerFromList : customer => dispatch( RemoveCustomerFromList( customer ) ),
    // AddCustomersToList : customers => dispatch( AddCustomersToList( customers ) )
});

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(ListCustomerSelectedComponent);