import React from 'react';

import ListCustomersComponent from './component/listCustomersComponent';
import ListCustomerSelectedComponent from './component/listCustomerSelectedComponent';
import SendEmailsComponent from './component/sendEmailsComponent';

import { Provider } from 'react-redux';

import {
    Link
} from "react-router-dom";

import {
    appStore
} from './stores/indexStore';

class SendEmailComponent extends React.Component {

    render() {
        return (
            <Provider store={appStore}>
                <div className="App">
                    <div className="header">
                        <Link to="/email-mkt/reports">Reports</Link>
                        <h3>Giao diện gửi mail MKT</h3>
                    </div>

                    <div className="body-content">
                        <ListCustomersComponent />

                        <ListCustomerSelectedComponent />

                        <SendEmailsComponent />
                    </div>
                </div>
            </Provider>
        )
    }
}

export default SendEmailComponent;