import React from 'react';

import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";

import SendEmailComponent from './sendMailComponent';
import ReportsComponent from './reportsComponent';


function App() {
    return (
        <Router>
            <Switch>
                <Route exact={true} path="/email-mkt/" component={SendEmailComponent}/>
                <Route exact={true} path="/email-mkt/reports" component={ReportsComponent}/>
            </Switch>
        </Router>
        
    );
}

export default App;
