import React from 'react';
import {
    Link
} from "react-router-dom";

import ReportCustomersComponent from './component/rpListCustomersComponent';
import ReportCustomerDetail from './component/rpCustomerDetailComponent';

class ReportsComponent extends React.Component {

    constructor( props ) {
        super( props );

        this.state = {
            customerSelected: null
        }
    }

    setCustomerSelected = ( customer )=> {
        this.setState({
            customerSelected: customer
        })
    }

    render( ) {
        
        let {
            customerSelected
        } = this.state;

        return(
            <div className="App">
                <div className="header">
                    <Link to="/email-mkt/">Gửi mail</Link>
                    <h3>Giao diện Report</h3>
                </div>

                <div className="body-content">
                    <ReportCustomersComponent setCustomer={this.setCustomerSelected} />
                    {
                        customerSelected && <ReportCustomerDetail customer={customerSelected} />
                    }
                </div>
            </div>
        )
    }
}

export default ReportsComponent;