import React from 'react';

class SendEmailsComponent extends React.Component {

    constructor( props ) {
        super( props )
    }

    render( ) {
        return (
            <div className="email-contents">
                <div className="header-title">
                    <h4>Thiết lập nội dung</h4>
                </div>
                <div className="contents">
                    <div className="form-group">
                        <label htmlFor="email-title">Tiêu đề</label>
                        <input type="text" className="form-control" id="email-title" placeholder="Tiêu đề" />
                    </div>

                    <div className="form-group group-content">
                        <label htmlFor="email-template">Template email</label>
                        <input type="file" className="form-control-file" id="email-template" />

                        <label htmlFor="email-preview">Preview</label>
                        <div className="preview">
                            
                        </div>
                    </div>

                    <div className="actions">
                        <button type="button" className="btn btn-success">
                            Gửi
                        </button>
                    </div>
                </div>
            </div>
        )
    }
 
}

export default SendEmailsComponent;