import React from 'react';

class ReportLineItemComponent extends React.Component {

    constructor( props ) {

        super(props);

        this.state = {
            show_error: false,
            show_link: false
        }
    }

    render() {
        let item = this.props.item;
        let {
            show_error,
            show_link
        } = this.state;
        
        return (
            <div className="letter-item">
                <div className="item">
                    { item.title }
                </div>
                <div className="item">
                    {
                        item.send_status  ? "Đã gửi" :
                        <section>
                            <span onClick={()=> this.setState( { show_error: !show_error } )}>
                                <span>Gửi lỗi</span>
                                {
                                    show_error ? <i className="fas fa-angle-up"></i>
                                    : <i className="fas fa-angle-down"></i>
                                }
                            </span>
                            {
                                show_error && 
                                <div className="error-detail">
                                    { item.send_desc }
                                </div>
                            }
                        </section>
                    }
                </div>
                <div className="item">
                    {
                        !item.read_status  ? "Chưa xem" :
                        <section>
                            <span onClick={()=> this.setState( { show_link: !show_link } )}>
                                <span>Đã xem</span>
                                {
                                    show_link ? <i className="fas fa-angle-up"></i>
                                    : <i className="fas fa-angle-down"></i>
                                }
                            </span>
                            {
                                show_link && 
                                <div className="link-detail">
                                    {
                                        item.read_links.map((link, index) => {
                                            return (
                                                <React.Fragment><a href={link} target="_blank" key={index}>{link}</a><br/></React.Fragment>
                                            )
                                        })
                                    }
                                </div>
                            }
                        </section>
                    }
                </div>
            </div>
        )
    }
}

export default ReportLineItemComponent;