import React from 'react';
import DatePicker from "react-datepicker";
import ReportLineItemComponent from './rpLineItemComponent';
import "react-datepicker/dist/react-datepicker.css";
import {
    data_letters
} from '../data';

class ReportCustomerDetail extends React.Component {

    constructor( props ) {
        super( props );
        this.state = {
            startDate: new Date(),
            endDate: new Date(),
        }
    }

    handleChange = date => {
        this.setState({
            startDate: date
        });
    };

    handleEndChange = date => {
        this.setState({
            endDate: date
        });
    };

    render() {
        let {
            customer
        } = this.props;


        return( 
            <div className="customer-details">
                <div className="header-title">
                    <h3>Thông tin customer</h3>
                </div>
                <div className="details">
                    <div className="forms">
                        
                        <div className="form-group">
                            <label htmlFor="customer_email">Email address</label>
                            <input type="email" className="form-control" id="customer_email" value={customer.email} readOnly={true} />
                        </div>

                        <div className="form-group">
                            <label htmlFor="customer_email">First Name</label>
                            <input type="email" className="form-control" id="customer_email" value={customer.first_name} />
                        </div>

                        <div className="form-group">
                            <label htmlFor="customer_email">Last Name</label>
                            <input type="email" className="form-control" id="customer_email" value={customer.last_name} />
                        </div>


                        <div className="form-group">
                            <label htmlFor="source">Nguồn từ</label>
                                <select className="form-control" id="source" name="source"  value={ customer.source.toUpperCase() }>
                                    <option value="">Tất cả</option>
                                    <option value="GEARN_COM" >GEARVN COM</option>
                                    <option value="GEARN_NEWS" >GEARVN NEWS</option>
                                    <option value="FACEBOOK" >FACEBOOK</option>
                                    <option value="GOOGLE" >GOOGLE</option>
                                </select>
                        </div>

                        <div className="form-group">
                            <label htmlFor="type">Loại</label>
                                <select className="form-control" id="type" name="type"  value={ customer.type.toUpperCase() }>
                                    <option value="">Tất cả</option>
                                    <option value="LEAD" >Tiềm năng</option>
                                    <option value="CUSTOMER" >Khách quen</option>
                                    <option value="OTHER" >Khác</option>
                                </select>
                        </div>

                        <div className="form-group">
                            <label htmlFor="status">Trạng thái</label>
                                <select className="form-control" id="status" name="status"  value={ customer.status.toUpperCase() }>
                                    <option value="">Tất cả</option>
                                    <option value="SUBSCRIBE" >Đăng ký</option>
                                    <option value="UNSUBSCRIBE" >Hủy đăng ký</option>
                                    <option value="DRAFT" >Rác</option>
                                    <option value="OTHER" >Khác</option>
                                </select>
                        </div>
                        <div className="actions">
                            <button type="button" className="btn btn-success">
                                Cập nhật
                            </button>
                        </div>
                    </div>
                    <div className="histories">
                        <div className="dates">
                            <label>
                                Từ ngày:
                                <DatePicker
                                    selected={this.state.startDate}
                                    onChange={this.handleChange}
                                />
                            </label>

                            <label>
                                Đến ngày:
                                <DatePicker
                                    selected={this.state.endDate}
                                    maxDate={this.state.startDate}
                                    onChange={this.handleEndChange}
                                />
                            </label>
                        </div>

                        <div className="letter-header">
                            <div className="item">Tiêu đề mail</div>
                            <div className="item">Trạng thái gửi</div>
                            <div className="item">Trạng thái xem</div>
                        </div>
                        
                        <div className="letter-body">
                        {
                            data_letters.map((item, index)=> {
                                return (
                                    <ReportLineItemComponent item={item} key={index} />
                                )
                            })
                        }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default ReportCustomerDetail;